export default {
	data() {
		return {
			//个人的数据
			head: "",
			nick: "",
			sno: "",
			realName: "",
			gender: "",
			phone: "",
			// 修改界面的数据
			heady: "",
			nicky: "",
			snoy: "",
			realNamey: "",
			gendery: "",
			phoney: "",
			seen: true,
			look: false
		}
	},
	methods: {
		tap: function() {
			wx.navigateBack({})
		},
		requestData() {
			this.$api.getUserInfo().then(res => {
				this.head = res.data.headPortrait;
				this.nick = res.data.nickName;
				this.sno = res.data.sno;
				this.realName = res.data.realName;
				if (res.data.sex == 1) {
					this.gender = "男"
				} else if (res.data.sex == 2) {
					this.gender = "女"
				}else{
					this.gender = "未知"
				}
				this.phone = res.data.phone;
			})
		},
		Edit1: function() {
			this.seen = false;
			this.look = true;
			
		},
		

		Edit2:function(){									
			this.$api.updateInfo({
			headPortrait : this.heady,
			nickName : this.nicky,
			sno:this.snoy,
			realName:this.realNamey,
			sex:this.gendery,
			phone:this.phoney						
		}).then(res=>{
				console.log(res)
			})
			this.seen = true
			this.look = false
		}


	},
	created() {
		this.requestData()
	}
	
	

}

